import Vue from 'vue'
import App from './App.vue'
import Buefy from 'buefy'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faVolumeUp, faVolumeMute, faUndoAlt, faInfo } from '@fortawesome/free-solid-svg-icons'
import { faDiscord, faTwitterSquare } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import './style.scss'

// icons
library.add(faVolumeUp, faVolumeMute, faUndoAlt, faInfo, faDiscord, faTwitterSquare)
Vue.component('vue-fontawesome', FontAwesomeIcon)

// buefy
Vue.use(Buefy, {
  defaultIconComponent: 'vue-fontawesome',
  defaultIconPack: 'fas'
})

Vue.config.productionTip = false

new Vue({
  render: h => h(App)
}).$mount('#app')
