<template>
  <div id="app" ref="app">
    <div class="app-container" v-show="allloaded">
      <div class="menu-container">
        <img class="left" src="../src/assets/logo.svg">
        <div class="mid"><span>{{timeMinSec[0]}}</span>:<span>{{timeMinSec[1]}}</span></div>
        <div class="right">
          <div class="buttons">
            <b-button type="is-primary" icon-right="undo-alt" v-on:click="reset" title="Reset"></b-button>
            <b-button type="is-info" icon-right="volume-up" title="Mute" v-show="!muted" v-on:click="mute" :disabled="isios"></b-button>
            <b-button type="is-info" icon-right="volume-mute" title="Unmute" v-show="muted" v-on:click="unmute" ></b-button>
            <b-button type="is-warning" icon-right="info" title="Info" v-on:click="infoActive = true" ></b-button>
          </div>
        </div>
      </div>
      <div class="game-container">
        <div class="one" v-bind:style="computedStyle">
          <video muted loop v-show="state === 0" ref="menu"></video>
          <img v-for="n in 74" :key="n" :src="frames[n - 1]" v-show="state === 1 && (counter === n || (counter === 0 && n === 1))" v-on:load.self.once="loaded++">
          <video muted loop v-show="state === 2" ref="gameover">
          </video>
        </div>
        <div class="two"></div>
        <div v-bind:style="computedStyle">
          <div id="konvacanvas" class="stack" v-bind:style="computedStyle"></div>
          <div class="stack startmenu" v-bind:style="computedStyle" v-show="state === 0">
            <div class="center">
              <div class="startmenu-text is-size-4-widescreen is-size-5-desktop is-size-6-touch">
                Click on Kaa to resist him. But be careful not to click on your guardian Bagheera.
              </div>
              <div class="startmenu-button center"><b-button type="is-primary" v-on:click="start">Start</b-button></div>
            </div>
          </div>
          <div class="stack rings" v-bind:style="opacity" v-show="state === 1 || state === 2">
            <video muted loop ref="rings">
            </video>
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="infoActive">
      <div class="modal-card" style="width: auto">
        <header class="modal-card-head">
          <p class="modal-card-title">Cease to Resist v0.3 (beta)</p>
          <button type="button" class="delete" v-on:click="infoActive = false"/>
        </header>
        <section class="modal-card-body">
          <div class="is-size-5 has-text-weight-bold">Made by Taro</div>
          <div class="is-flex is-flex-direction-row is-align-items-center">
            <b-icon pack="fab" icon="twitter-square" size="is-large"></b-icon>
            <a class="has-text-weight-bold" href="https://twitter.com/Tarolewd" target="_blank">@Tarolewd</a>
          </div>
          <div class="is-flex is-flex-direction-row is-align-items-center">
            <b-icon pack="fab" icon="discord" size="is-large"></b-icon>
            <a class="has-text-weight-bold" href="https://discord.com/invite/V2EKfyDpV6" target="_blank">Kaa's Treetop</a>
          </div>
          <div class="is-size-5 has-text-weight-bold mt-4">Sources</div>
          <div class="content">
            <ul class="mt-0">
              <li>'The Jungle Book' belongs to Disney</li>
              <li>'Kaa ASMR' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UCdFPB-7oRVyT74kBaDwid5Q" target="_blank">m5a5e5</a></li>
              <li>'Hypnotic Rings' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UC8xGQOErVZSlM3PRHjoMe0A" target="_blank">K VFX</a></li>
            </ul>
          </div>
        </section>
        <footer class="modal-card-foot"></footer>
      </div>
    </b-modal>
  </div>
</template>

<script>
const Konva = require('konva')

export default {
  name: 'App',
  data: function () {
    return {
      bodyWidth: 0,
      bodyHeight: 0,
      konvaCache: {},
      state: 0,
      counter: 0,
      time: 0,
      intervall: 1000,
      frames: [],
      sounds: ['gulp', 'ping', 'asmr', 'doom'],
      loaded: 0,
      allloaded: false,
      timeouts: {},
      muted: false,
      infoActive: false,
      isios: false
    }
  },
  computed: {
    timeMinSec: function () {
      let minutes = Math.floor(this.time / 60)
      let seconds = this.time - minutes * 60
      if (minutes < 10) minutes = `0${minutes}`
      if (seconds < 10) seconds = `0${seconds}`
      return [minutes, seconds]
    },
    dimensions: function () {
      const width = Math.floor(((this.bodyWidth - 35) / 2))
      const height = Math.floor(1072 * (width / 1872))
      return [width, height]
    },
    computedStyle: function () {
      return {
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    },
    opacity: function () {
      let opacity = 0
      if (this.state === 2) {
        opacity = 1
      } else {
        if (this.counter > 8) opacity = (this.counter - 9) * 1 / 65
      }
      return {
        opacity,
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    }
  },
  watch: {
    loaded: function () {
      if (this.loaded === 81) {
        const loadElement = document.getElementById('load-container')
        loadElement.remove()
        this.allloaded = true
      }
    },
    dimensions: function () {
      this.konvaCache.stage.size({
        width: this.dimensions[0],
        height: this.dimensions[1]
      })
      this.redrawImages()
    }
  },
  methods: {
    updateBodyDimensions: function () {
      this.bodyWidth = document.body.offsetWidth
      this.bodyHeight = document.body.bodyHeight
      this.konvaCache.scale = 1
      if (this.bodyWidth < 1700) this.konvaCache.scale = Math.max(this.bodyWidth / 1700, 0.5)
    },
    initKonva: function () {
      const stage = new Konva.Stage({
        container: 'konvacanvas',
        width: this.dimensions[0],
        height: this.dimensions[1],
        name: 'stage'
      })
      stage.on('mousedown touchstart', (event) => {
        const target = event.target
        if (target.name().startsWith('kaa')) {
          event.target.destroy()
          layer.batchDraw()
          this.counter--
          return
        }
        if (target.name().startsWith('bagheera')) {
          event.target.destroy()
          layer.batchDraw()
          this.counter++
        }
      })
      const layer = new Konva.Layer()
      stage.add(layer)

      this.konvaCache.kaas = []
      for (let i = 1; i < 5; i++) {
        const imageObj = new Image()
        imageObj.src = require(`../src/assets/kaa-${i}.png`)
        const kaa = new Konva.Image({
          image: imageObj,
          width: 80,
          height: 80,
          name: `kaa-${i}`
        })
        this.konvaCache.kaas.push(kaa)
      }

      this.konvaCache.bagheeras = []
      for (let i = 1; i < 3; i++) {
        const imageObj = new Image()
        imageObj.src = require(`../src/assets/bagheera-${i}.png`)
        const bagheera = new Konva.Image({
          image: imageObj,
          width: 80,
          height: 80,
          name: `bagheera-${i}`
        })
        this.konvaCache.bagheeras.push(bagheera)
      }

      this.konvaCache.stage = stage
      this.konvaCache.layer = layer
    },
    initFrames: function () {
      for (let i = 1; i < 75; i++) {
        this.frames.push(require(`../src/assets/frames/frame-${i}.jpg`))
      }
    },
    initSounds: function () {
      const sounds = {}
      this.sounds.forEach(soundName => {
        const sound = new Audio()
        sound.preload = 'auto'
        sound.addEventListener('canplaythrough', () => this.loaded++, { once: true })
        sound.volume = 0
        sound.src = require(`../src/assets/sounds/${soundName}.mp3`)
        sound.load()
        sounds[soundName] = sound
      })
      this.sounds = sounds
      this.isios = this.sounds.gulp.volume !== 0
    },
    initVideos: function () {
      const videos = ['menu', 'rings', 'gameover']
      videos.forEach(videoName => {
        const video = this.$refs[videoName]
        video.preload = 'auto'
        video.addEventListener('canplaythrough', () => this.loaded++, { once: true })
        if (videoName === 'menu') video.autoplay = true
        const source = document.createElement('source')
        source.setAttribute('src', require(`../src/assets/videos/${videoName}.mp4`))
        source.setAttribute('type', 'video/mp4')
        video.appendChild(source)
        video.load()
      })
    },
    addImage: function () {
      let clone = null
      if (Math.random() > 0.1) {
        const random = Math.floor(Math.random() * 4)
        clone = this.konvaCache.kaas[random].clone({
          scaleX: this.konvaCache.scale,
          scaleY: this.konvaCache.scale,
          x: Math.random() * (this.konvaCache.stage.width() - 80 * this.konvaCache.scale),
          y: Math.random() * (this.konvaCache.stage.height() - 80 * this.konvaCache.scale)
        })
        this.counter++
      } else {
        const random = Math.floor(Math.random() * 2)
        clone = this.konvaCache.bagheeras[random].clone({
          scaleX: this.konvaCache.scale,
          scaleY: this.konvaCache.scale,
          x: Math.random() * (this.konvaCache.stage.width() - 80 * this.konvaCache.scale),
          y: Math.random() * (this.konvaCache.stage.height() - 80 * this.konvaCache.scale)
        })
      }
      this.konvaCache.layer.add(clone)
      this.konvaCache.layer.batchDraw()
    },
    redrawImages: function () {
      const images = this.konvaCache.layer.getChildren()
      images.each((image) => {
        image.scaleX(this.konvaCache.scale)
        image.scaleY(this.konvaCache.scale)
        image.x(Math.random() * (this.konvaCache.stage.width() - 80 * this.konvaCache.scale))
        image.y(Math.random() * (this.konvaCache.stage.height() - 80 * this.konvaCache.scale))
      })
      this.konvaCache.layer.batchDraw()
    },
    running: function () {
      if (this.state !== 1) return
      if (this.time > 0 && this.time % 5 === 0) {
        this.intervall = 1000 - Math.floor(300 * (Math.log(this.time / 5 + 1) / Math.log(3)))
      }
      this.timeouts.running = setTimeout(() => {
        if (this.state !== 1) return
        this.addImage()
        if (this.counter < 75) {
          if (this.counter === 57) this.sounds.gulp.play()
          this.running()
        } else {
          this.sounds.ping.play()
          this.sounds.doom.play()
          this.$refs.gameover.play()
          this.state = 2
        }
      }, this.intervall)
    },
    timer: function () {
      this.timeouts.timer = setTimeout(() => {
        if (this.state !== 1) return
        if (this.counter < 75) {
          this.timer()
          this.time++
        }
      }, 1000)
    },
    start: function () {
      this.$refs.menu.pause()
      this.$refs.menu.currentTime = 0
      this.$refs.rings.play()
      Object.entries(this.sounds).forEach(([key, value]) => {
        value.play()
        value.pause()
        if (key === 'asmr') {
          value.volume = 0.7
        } else {
          value.volume = 1
        }
      })
      setTimeout(() => {
        if (this.state !== 1) return
        this.sounds.asmr.currentTime = 0
        this.sounds.asmr.play()
      }, 2000)
      this.state = 1
      this.running()
      this.timer()
    },
    reset: function () {
      if (this.timeouts.running) {
        clearTimeout(this.timeouts.running)
        this.timeouts.running = null
      }
      if (this.timeouts.timer) {
        clearTimeout(this.timeouts.timer)
        this.timeouts.timer = null
      }
      this.konvaCache.layer.destroyChildren()
      this.konvaCache.layer.batchDraw()
      this.sounds.asmr.pause()
      this.sounds.asmr.currentTime = 0
      this.$refs.rings.pause()
      this.$refs.rings.currentTime = 0
      if (this.state === 2) {
        this.$refs.gameover.pause()
        this.$refs.gameover.currentTime = 0
      }
      this.$refs.menu.play()
      this.counter = 0
      this.time = 0
      this.intervall = 1000
      this.state = 0
    },
    mute: function () {
      Object.values(this.sounds).forEach(sound => {
        sound.muted = true
      })
      this.muted = true
    },
    unmute: function () {
      Object.values(this.sounds).forEach(sound => {
        sound.muted = false
      })
      this.muted = false
    }
  },
  mounted: function () {
    this.updateBodyDimensions()
    this.initVideos()
    this.initKonva()
    this.initSounds()
    this.initFrames()
    window.addEventListener('resize', this.updateBodyDimensions)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.updateBodyDimensions)
  }
}
</script>

